<!--  -->
<template>
  <div class="genderBox">
    <van-picker
      show-toolbar
      :columns="columns"
      @cancel="$emit('genderBoxClose')"
      @confirm="onConfirm"
      :default-index='value'
    />
  </div>
</template>

<script>
import { setUserInfo } from '@/api/user.js'
export default {
  name: 'genderBox',
  components: {},
  props: {
    // 
    value: {
      type: [String, Number],
      required: true
    }
  },
  data () {
    return {
      columns: ['', ''] // 
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    // 
    async onConfirm (value) {
      // 
      const valuesIndex = this.columns.indexOf(value)
      console.log(valuesIndex)
      await setUserInfo({
        gender: valuesIndex
      })
      // 
      this.$emit('genderBoxClose')
      // v-model
      this.$emit('input', valuesIndex)
    }
  }
}
</script>

