<template>
  <div class="setmy-box">
    <navbar>
      <i slot="left" class="icon icon-youjiantou setmy-title" @click="$router.back()"></i>
      <p slot="title" class='setmy-title'></p>
    </navbar>
    <van-cell title="" is-link @click='$refs.files.click()'>
      <van-image
        round
        width='35px'
        height='35px'
        :src="userProfileData.data.photo"
        slot="default"
        fit="cover"
      />
    </van-cell>
    <van-cell title="" is-link  :value="userProfileData.data.name" @click="isNameBox = true"/>
    <van-cell
      title=""
      is-link
      :value="userProfileData.data.gender === 1 ? '' : ''"
      @click='isGenderBox = true'
    />
    <van-cell title="" is-link :value="userProfileData.data.birthday" @click="isOldBox = true"/>

    <!-- ,inputhiddeninput -->
    <input
      type="file"
      hidden
      ref='files'
      accept='image/*'
      @change='onChange'
    >
    <!--   -->
    <van-popup position="bottom" v-model="isPhotoBox" :style="{ height: '100%' }" >
      <photoBox
        :file='file'
        @closePhotoBox='isPhotoBox = false'
        @setPhoto="userProfileData.data.photo = $event"
      ></photoBox>
    </van-popup>
    <!--  -->
    <van-popup position="bottom" :style="{ height: '100%' }" v-model="isNameBox">
      <nameBox v-if="isNameBox"
        @nameBoxClose="isNameBox = false"
        v-model="userProfileData.data.name"
      ></nameBox>
    </van-popup>
    <!--  -->
    <van-popup position="bottom" v-model="isGenderBox">
      <genderBox
        v-if='isGenderBox'
        @genderBoxClose='isGenderBox=false'
        v-model='userProfileData.data.gender'>
      </genderBox>
    </van-popup>
    <!--  -->
    <van-popup position="bottom" v-model="isOldBox">
      <old-box
        v-if="isOldBox"
        v-model="userProfileData.data.birthday"
        @closeOldBox="isOldBox=false"
      >
      </old-box>
    </van-popup>
  </div>
</template>

<script>
// 
import navbar from '../../../components/navbar'
import { userProfile } from '../../../api/user'
import oldBox from './oldBox'
import nameBox from './nameBox'
import genderBox from './genderBox'
import photoBox from './photoBox'
export default {
  name: 'setMy',
  data () {
    return {
      userProfileData: '', // 
      isNameBox: false, // 
      isGenderBox: false, // 
      isOldBox: false, // 
      isPhotoBox: false, // 
      file: '' // 
    }
  },
  components: {
    navbar,
    nameBox,
    genderBox,
    oldBox,
    photoBox
  },
  created () {
    this.getUserProfile() // 
  },
  methods: {
    // 
    async getUserProfile () {
      const { data } = await userProfile()
      this.userProfileData = data
    },
    // ()
    onChange (event) {
      // console.log(event.target.files[0]) // 
      this.file = event.target.files[0] // 
      this.isPhotoBox = true // 
      // input
      event.target.value = ''
      // this.$refs.files.value ''
    }
  }
}
</script>

<style scoped>
  .theme-colors {
      background-color: #1989fa;
  }
  .van-popup{
    background-color: #f8f8f8;
  }
  .setmy-title{
    color: #fff;
  }
  .van-cell{
    display: flex;
    align-items: center;
  }
  .van-popup{
    background-color: #666
  }
</style>
