<template>
  <div class="photoBox">
    <img :src="photo" alt="" ref='images' class='updata-img'>
    <div>
      <p @click="$emit('closePhotoBox')"></p>
      <p @click='getPhoto'></p>
    </div>
  </div>
</template>

<script>
import { setPhoto } from '@/api/user.js'
// TODO js
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
export default {
  name: 'photoBox',
  components: {},
  props: {
    // 
    file: {
      // type: [String, Object],
      required: true
    }
  },
  data () {
    return {
      // urlimg
      photo: window.URL.createObjectURL(this.file)
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {
    // TODO DOM
    const image = this.$refs.images
    // 
    const cropper = new Cropper(image, {
      aspectRatio: 1, // 
      viewMode: 1,
      dragMode: 'move',
      cropBoxMovable: false, // 
      cropBoxResizable: false, // 
      background: false, // 
      movable: true, // 
      highlight: true
    })
    console.log(cropper)
  },
  methods: {
    async getPhoto () {
      try {
        // loding
        this.$toast.loading({
          message: '...',
          forbidClick: true,
          duration: 0
        })

        const fd = new FormData() // FromData
        fd.append('photo', this.photo) // FromData fd

        // 
        await setPhoto(fd)
        this.$emit('setPhoto', this.photo) // 
        this.$toast.success('')
      } catch (err) {
        // console.log(err)
        this.$toast.fail('')
      }
      this.$emit('closePhotoBox') // 
    }
  }
}
</script>

<style scoped>
.updata-img{
  background-color: #666
}
.photoBox>div{
  position:fixed;
  bottom: 0;
  padding: 0 12px;
  width: 100%;
  box-sizing:border-box;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center
}
.photoBox>img{
  display: block;
  max-width: 100%
}
.photoBox>div>*{
  font-size: 14px;
  color: #fff;
}
</style>
