<!--  -->
<template>
  <div class="nameBox">
    <!---->
    <navbar class="nameBoxTop">
      <i class="icon icon-guanbi" slot="left" @click="$emit('nameBoxClose')"></i>
      <p slot="title"></p>
      <p slot="right" @click='setName'></p>
    </navbar>
    <div class="nameText">
      <van-field
        v-model="message"
        rows="2"
        autosize
        type="textarea"
        maxlength="7"
        placeholder=""
        show-word-limit
      />
    </div>
  </div>
</template>

<script>
import navbar from '../../../components/navbar'
// 
import { setUserInfo } from '@/api/user'
export default {
  name: 'nameBox',
  data () {
    return {
      message: '' // 
    }
  },
  props: {
    // oldName: {
    //   type: String,
    //   required: true
    // }
    value: {
      type: String,
      required: true
    }
  },
  components: {
    navbar
  },
  methods: {
    // 
    async setName () {
      try {
        // 
        this.$toast.loading({
          message: '...',
          forbidClick: true,
          duration: 0
        })
        // 
        await setUserInfo({
          name: this.message.String
        })
        // 
        this.$toast.success('')
        // 
        // this.$emit('nameBoxCloses', this.message)
        this.$emit('nameBoxClose') // 
        this.$emit('input', this.message) // name
      } catch (err) {
        if (err && err.response && err.response.status === 409) {
          // 
          this.$toast.fail('')
        } else {
          this.$toast.fail('')
        }
      }
    }
  },
  created () {
    // 
    this.message = this.value
  }
}
</script>

<style scoped>
  .theme-colors {
      background-color: #1989fa;
  }
  .nameBoxTop *{
    color: #fff;
  }
  .nameText{
    padding: 12px;
  }
</style>
