<template>
  <div>
    <van-datetime-picker
      v-model="currentDate"
      type="date"
      title=""
      :min-date="minDate"
      :max-date="maxDate"
      @confirm="isOld"
      @cancel="$emit('closeOldBox')"
    />
  </div>
</template>

<script>
import { setUserInfo } from '@/api/user.js'
export default {
  name: 'oldBox',
  props: {
    // 
    value: {
      type: String,
      require: true
    }
  },
  data () {
    return {
      minDate: new Date(0, 0, 1), // 
      maxDate: new Date(), //  
      currentDate: new Date(this.value) // 
    }
  },
  methods: {
    // value
    async isOld (value) {
      const times = `${value.getFullYear()}-${value.getMonth() + 1}-${value.getDate()}` // new time()
      // console.log(times)
      // loding
      try {
        this.$toast.loading({
          message: '...',
          forbidClick: true,
          duration: 0
        })
        await setUserInfo({
          birthday: times
        })
        // 
        this.$toast.success('')
        this.$emit('input', times)
        this.$emit('closeOldBox')
      } catch (err) {
        this.$toast.fail('')
      }
    }
  }
}
</script>

